import { useState } from 'react';

import './hero.scss';

import heroImage from '../../image/bg-1.jpg';
import logo from '../../image/logo.svg';

const Hero = () => {
    const [isOpenPopUp, setIsOpenPopUp] = useState(false);

    const togglePopUp = () => {
        setIsOpenPopUp(!isOpenPopUp);
    }
    return (
        <div className="hero">
            {isOpenPopUp ? <span className="popup-rectangle" onClick={()=> togglePopUp()}></span> : null}
            <div className="hero-wrapper">
                <img className="hero-bg" src={heroImage} alt="background"/>
                <img className="logo" src={logo} alt="logo"/>
            </div>
            <button 
                className="hero-button" 
                href="#"
                onClick={()=> togglePopUp()}></button>
            <div className={`popup ${isOpenPopUp ? 'popup-open' : ''}`}>
                <form className="popup__form">
                    <input 
                        className="popup__form-input" 
                        type="text"
                        placeholder=" "/>
                    <input 
                        className="popup__form-input" 
                        type="tel"
                        placeholder=""/>
                    <textarea 
                        className="popup__form-input textarea" 
                        type="text"
                        placeholder=""/>
                    <input className="popup__form-button" type="submit"/>
                </form>
                <div className="popup__description">
                    <h4 className="popup__description-title"> </h4>
                    <ul className="popup__rules">
                        <li className="popup__rules-item">
                            <h5 className="popup__rules-title"></h5>
                            <span className="popup__rules-text">  13:00</span>
                        </li>
                        <li className="popup__rules-item">
                            <h5 className="popup__rules-title"></h5>
                            <span className="popup__rules-text"> 11:00</span>
                        </li>
                        <li className="popup__rules-item">
                            <h5 className="popup__rules-title">  </h5>
                            <span className="popup__rules-text"> 2  4 </span>
                        </li>
                    </ul>
                    <ul className="popup__list">
                        <li className="popup__list-item">  </li>
                        <li className="popup__list-item">  </li>
                        <li className="popup__list-item"> </li>
                        <li className="popup__list-item"> </li>
                        <li className="popup__list-item">  </li>
                    </ul>
                    <p className="popup-price">10000  - <span></span></p>
                </div>
            </div>
        </div>
    )
}

export default Hero;